@font-face {
  font-family: Quicksand;
  src: url(../font/Quicksand-VariableFont_wght.ttf);
}
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: quicksand;
}



.nav-items{
  background-color: #FFFAFA;
  filter: drop-shadow(2px 0.4px 12px #ddd2d2);
  height: 60px;
  font-family: quicksand;
  font-size: 19px; 
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 20px;
  float: right;
  margin-bottom: -5px;
}

.navbar-nav {
  list-style-type: none;
  display: flex;
}

.nav-item {
  margin-right: 20px;
}

.nav-link {
  color: #810c0c;
  text-decoration: none;
  transition: color 0.3s ease;
}

.nav-link:hover {
  color: #db1a1a;
  opacity: 0.7;
}

.container {
  width: 100%;
  height: 100vh;
  background-image: url('../images/bkg-1.jpg'); /* Replace with your image path */
  background-size:cover;
  background-position: center;
  display: flex;
  align-items: center;
  
}


.content {
  position: relative;
  z-index: 1;
  max-width: 600px;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.7); /* Semi-transparent white background */
  border-radius: 10px;
  text-align: left;
  color: #9A0200;
  margin-left: 20px;
}

h1 {
  font-size: 36px;
  font-weight: bold;
  color: #9A0200;
}

p {
  font-size: 18px;
  color: #555;
}